@import "variable";
@import "mixins";

section:nth-of-type(1) {
  .container {
    .galleries {
      li:first-of-type {
        padding-bottom: calc(#{$padding * 4});
        img {
          max-width: $percentage;
          height: $margin-auto;
          border-radius: calc(#{$padding * 2.5});
        }
      }
      li:nth-of-type(2) {
        ul {
          @include flex;
          overflow-x: scroll;
          scrollbar-width: none;
          li {
            img {
              max-width: calc(#{$max-width * 8.15});
              border-radius: calc(#{$padding * 2.5});
            }
          }
          li:not(:first-of-type) {
            padding-left: calc(#{$padding * 5});
            img {
              opacity: .5;
            }
          }
          li:not(:first-of-type):hover {
            img {
              opacity: 1;
            }
          }
        }
        ul::-webkit-scrollbar {
          display: none;
        }
      }
    }
  }
}